import React from 'react';
import {inject} from "mobx-react";
import {hot} from "react-hot-loader";
import {getLang} from "@web3game/lang";
import {List, ListItem, Page} from "react-onsenui";
import {popPage} from "@web3game/common/utils/navigatorHelper";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {getLocaleList, setLocale} from "../../../lang/localeHelper";
import {createHeaderRender} from "../Header";


export type LanguageSelectorProps = {
    list: object[];
    onSelect: (locale: string) => void;
};

const onSelectLanguage = (locale: string) => {
    setLocale(locale);
    popPage();
};

export function LanguageSelectorPure(
    {
        list = getLocaleList(),
        onSelect = onSelectLanguage,
    }: LanguageSelectorProps,
) {
    const renderFixed = createHeaderRender(getLang().LANGUAGE_SETTING);

    return (
        <Page renderFixed={renderFixed}>
            <PagePadder />
            <List
                modifier="inset"
                dataSource={list}
                renderRow={({ locale, text }, idx) => (
                    <ListItem
                        key={idx}
                        modifier="longdivider"
                        onClick={() => onSelect(locale)}
                    >
                        <div>
                            {`${text} (${locale})`}
                        </div>
                    </ListItem>
                )}
            />
        </Page>
    );
}

export const LanguageSelectorPage = compose<LanguageSelectorProps>(
    hot(module),
    inject(
        ({ accountStore }) => ({
            onSelect: (locale: string) => {
                onSelectLanguage(locale);
                accountStore.init({
                    locale,
                });
            },
        }),
    ),
)(LanguageSelectorPure);

export default LanguageSelectorPage;
